#job-submission-form {
    max-width: 800px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
}

#job-submission-form h2 {
    font-size: 24px;
    margin-bottom: 5px;
}

.required-fields {
    color: #ff0000;
    font-size: 12px;
    text-align: right;
    margin-top: 0;
}

.form-group {
    margin-bottom: 20px;
}

.form-row {
    display: flex;
    gap: 20px;
}

.col-md-6 {
    flex: 1;
}

label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

input[type="text"],
select,
.select2-container {
    width: 100%;
    padding: 0px;
    border: none;
    border-radius: 4px;
}

.radio-group {
    display: flex;
    gap: 20px;
}

small {
    display: block;
    color: #666;
    font-size: 12px;
    margin-top: 5px;
}

input[type="submit"] {
    background-color: #0073aa;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

input[type="submit"]:hover {
    background-color: #005177;
}

/* Style the wp_editor */
.wp-editor-container {
    border: 1px solid #ccc;
}

.wp-editor-tabs {
    margin-bottom: 0;
}

/* Style Select2 dropdowns */
.select2-container--default .select2-selection--single,
.select2-container--default .select2-selection--multiple {
    border: 1px solid #ccc;
    border-radius: 4px;
}

.form-step {
    margin-bottom: 20px;
}
